/* Universal reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; /* Ensure padding/border doesn't affect layout */
}

/* Body styles with fallback background */
body {
  background: url(https://i.pinimg.com/564x/75/98/19/759819a4cce0f0310e96c7c1b4167e7e.jpg) no-repeat center center fixed;
  background-size: cover;
  background-color: #f0f0f0; /* Fallback background color */
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

@media (max-width: 768px) {
  body {
    background-image: none; /* Remove the background image on smaller screens */
    background-color: #f0f0f0; /* Fallback color */
  }
}

/* App container */
.app {
  margin-top: 30px;
  text-align: center;
  color: rgba(0, 0, 0, 0.885);
  font-weight: 300;
}

/* Wrap the input and button together in a flex container */
.input-container {
  display: flex;
  justify-content: center; /* Align items horizontally */
  align-items: center; /* Vertically center items */
  gap: 10px; /* Add space between input and button */
}

/* Input field styles */
.app input {
  font-size: 16px;
  border-radius: 5px;
  border: 2px solid black;
  outline: none;
  padding: 10px 5px;
  width: 200px;
  margin-bottom: 20px; /* Space below input */
}

.app input:focus {
  border-color: #007bff; /* Highlight border on focus */
  outline: none;
}

/* Button styles with centering */
button {
  background-color: #007bff;
  color: white;
  border: none;
  outline: none;
  margin-left: 20px;
  padding: 10px 30px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.2s ease-in-out;
}

button:hover {
  background-color: #ad00b3;
  transform: scale(1.05); /* Slightly scale up on hover */
}

button:focus {
  outline: 2px solid #ad00b3;
}

/* Navigation styles */
nav {
  background-color: rgb(81, 1, 81);
  padding: 20px 0;
  display: flex;
  justify-content: center;
  margin-top: 5px;
  font-size: 20px;
}

nav ul {
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  color: rgb(255, 255, 255);
  font-weight: bold;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

nav ul li {
  margin: 0 15px; /* Add horizontal spacing between nav items */
}

nav ul li:hover {
  transform: scale(1.4);
  transition: transform 0.3s ease; /* Add smooth transition */
}

.navActive {
  color: rgb(255, 47, 203);
  font-size: 30px;
  font-weight: bold;
}

/* App container for profile data */
.app-container {
  background-color: rgb(0, 179, 255);
  color: white;
  padding: 20px;
  font-size: 20px;
  font-weight: 100;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

/* Responsive Design: Adjust layout for smaller screens */
@media (max-width: 768px) {
  .input-container {
    flex-direction: column;
    align-items: center;
  }

  .app input {
    width: 100%;
    margin-bottom: 20px;
  }

  button {
    width: 60%;
    margin: 0 auto 20px;
  }

  nav ul {
    width: 80%;
    flex-direction: column;
    align-items: flex-start;
  }

  nav ul li {
    margin-bottom: 10px;
  }

  .app-container {
    font-size: 16px;
  }
}
